import React, { Component } from 'react'
import { connect } from 'react-redux'
import { posY } from '../../converters/calculatePosY'
import { posX } from '../../converters/calculatePosX'
import {appointmentUpdate, deleteAppointment} from '../../../actions/appointmentActions'
import {scheduleShowAvailabilityUpdate} from '../../../actions/scheduleActions'
class Appointment extends Component {
  constructor(props) {
    super(props);
    this.props = props
    this.posX = posX
    this.posY = posY
    this.state = {
      isDragging: false,
      currentX: 0,
      currentY: 0,
      doRender: true,
    }
  }

  componentWillMount() {
    const { appointments, appointmentID, numDays, widthSchedule, heightSchedule} = this.props
    const appointment = appointments[appointmentID]
    this.setState({
      currentX: this.posX(appointment.startTime, numDays, widthSchedule),
      currentY:  this.posY(appointment.startTime, heightSchedule),
    }
    )
  }

  componentWillUnmount() {
    window.removeEventListener('mousemove', this.handleMouseMove);
    window.removeEventListener('mouseup', this.handleMouseUp);
  }

  setPosition = (clientX, clientY) => {
    const rect = document.getElementById("schedule-background").getBoundingClientRect();
    let x = clientX - rect.left
    let y = clientY - rect.top;
    this.setState({
      currentX: x,
      currentY: y
    });
  }
  handleMouseDown = ({ clientX, clientY }) => {
    window.addEventListener('mousemove', this.handleMouseMove);
    window.addEventListener('mouseup', this.handleMouseUp);
    this.setState({
      isDragging: true
    });
    this.props.turnOnShowAvailability(this.props.appointments[this.props.appointmentID].client)
    this.setPosition(clientX, clientY)
  };

  handleMouseMove = ({ clientX, clientY}) => {
    const { isDragging } = this.state;
    if (!isDragging) {
      return;
    }
    this.setPosition(clientX, clientY)
  };

  handleMouseUp = () => {
    window.removeEventListener('mousemove', this.handleMouseMove);
    window.removeEventListener('mouseup', this.handleMouseUp);
    this.setState(
      {
        isDragging: false
      }
    );
    this.props.turnOffShowAvailability()

    const {currentX, currentY} = this.state
    if(this.state.currentX < 0 || this.state.currentY < 0){
      this.setState({
        doRender: false
      })
      this.props.removeAppointment()
      
      return 
    }
    const numMinutesTot = Math.floor(currentY * 24 * 4  /this.props.heightSchedule) * 15
    const currentDay = Math.floor((currentX / this.props.widthSchedule) * this.props.numDays)
    
    const newAppt = this.props.appointments[this.props.appointmentID]
    newAppt.startTime = newAppt.startTime.startOf('week').plus({days: currentDay})

    newAppt.startTime = newAppt.startTime.startOf('day').plus({minutes: numMinutesTot})
    this.props.updateAppt(newAppt)
  };

  newStyle(appointment, client, numDays, widthSchedule, heightSchedule){
    const fullWidth = widthSchedule/numDays
    const width = fullWidth * 0.85
    const height = parseInt((appointment.lengthMinutes) / (24 * 60) * heightSchedule)
    let x, y
    if(this.state.isDragging){
      x = this.state.currentX - width/2
      y = this.state.currentY
    } else {
      x = this.posX(appointment.startTime, numDays, widthSchedule) + fullWidth * 0.15
      y = this.posY(appointment.startTime, heightSchedule)
    }
    
    let border = 0
    if(this.state.currentX < 0 || this.state.currentY < 0){
      border = 5
    }
    return {
      background: client.color,
      borderWidth: border,
      width: width + 'px',
      height: height + "px",
      transform: `translate(${x}px, ${y}px)`}
  }
    
  render(){
    const { appointments, clients, appointmentID, numDays, widthSchedule, heightSchedule} = this.props
    const appointment = appointments[appointmentID]
    
    const clientId = appointment.client
    const name = clients[clientId].name

    const style = this.newStyle(
        appointment, 
        clients[clientId],
        numDays, 
        widthSchedule, 
        heightSchedule)
    
    return (
      <div key={"master_appointment" + appointmentID} >
    {this.state.doRender ? <div 
      onMouseDown={this.handleMouseDown}
      key={appointmentID} 
      id={"appointment_"+appointmentID} 
      className="appointment" 
      style={style}
      >
        {name}
    </div>: null }
    </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    appointments: state.appointments,
    clients: state.clients,
    numDays: state.schedule.numDays,
    widthSchedule: state.schedule.widthSchedule,
    heightSchedule: state.schedule.heightSchedule,
  }
}

const mapDispatchToProps = (dispatch, props) => {
  return {
    updateAppt: (newAppt) => dispatch(appointmentUpdate(props.appointmentID, newAppt)),
    turnOnShowAvailability: (id) => dispatch(scheduleShowAvailabilityUpdate(id)),
    turnOffShowAvailability: () => dispatch(scheduleShowAvailabilityUpdate(null)),
    removeAppointment: () => dispatch(deleteAppointment(props.appointmentID)),
  }
  
}

export default connect(mapStateToProps, mapDispatchToProps)(Appointment)


