import React, { Component } from 'react'
import { connect } from 'react-redux'
import { posY } from '../../converters/calculatePosY'
import { posX } from '../../converters/calculatePosX'

class Availability extends Component {
  constructor(props) {
    super(props);
    this.props = props
    this.posX = posX
    this.posY = posY
    this.state = {
      isDragging: false,
      isExtending: false,
      currentX: 0,
      currentY: 0,
      doRender: true,
    }
  }

  componentWillMount() {
    const { startTime, 
      color,
      lengthMinutes, 
      numDays, widthSchedule, heightSchedule} = this.props
    this.setState({
      startTime, 
      color,
      lengthMinutes, 
    })
    this.setState({
      currentX: this.posX(startTime, numDays, widthSchedule),
      currentY:  this.posY(startTime, heightSchedule),
    }
    )
  }

  componentWillUnmount() {
    window.removeEventListener('mousemove', this.handleMouseMove);
    window.removeEventListener('mouseup', this.handleMouseUp);
  }

  setPosition = (clientX, clientY) => {
    const rect = document.getElementById("schedule-background").getBoundingClientRect();
    let x = clientX - rect.left
    let y = clientY - rect.top;
    
    this.setState({
      currentX: x,
      currentY: y
    });
  }

  handleExtentMouseDown= () => {
    window.addEventListener('mousemove', this.handleExtentMouseMove);
    window.addEventListener('mouseup', this.handleExtentMouseUp);
    
    this.setState({
      isDragging: false, 
      isExtending: true,
    });
  };

  handleExtentMouseMove = ({ clientY }) => {
    const {heightSchedule} = this.props
    const {startTime} = this.state
    const currentY = this.posY(startTime, heightSchedule)
    const rect = document.getElementById("schedule-background").getBoundingClientRect();
    const y = clientY - rect.top;
    let heightMinutes = Math.round((y - currentY) / heightSchedule * 24 * 4) * 15

    heightMinutes = heightMinutes < 15 ? 15: heightMinutes
    this.setState({
      lengthMinutes: heightMinutes
    })
  }

  handleExtentMouseUp = () => {
    window.removeEventListener('mousemove', this.handleExtentMouseMove);
    window.removeEventListener('mouseup', this.handleExtentMouseUp);
    this.setState(
      {
        isExtending: false
      }
    );
    this.props.handleSingleAvailabilityChange(this.props.id, {
      color: this.state.color,
      lengthMinutes: this.state.lengthMinutes,
      startTime: this.state.startTime
    })
  }

  handleMouseDown = ({ clientX, clientY }) => {
    window.addEventListener('mousemove', this.handleMouseMove);
    window.addEventListener('mouseup', this.handleMouseUp);

    this.setState({
      isDragging: true
    });
    this.setPosition(clientX, clientY)
  };

  handleMouseMove = ({ clientX, clientY}) => {
    const { isDragging } = this.state;
    if (!isDragging) {
      return;
    }
    this.setPosition(clientX, clientY)
  };

  handleMouseUp = () => {
    window.removeEventListener('mousemove', this.handleMouseMove);
    window.removeEventListener('mouseup', this.handleMouseUp);
    this.setState(
      {
        isDragging: false
      }
    );

    const {currentX, currentY} = this.state
    if(this.state.currentX < 0 || this.state.currentY < 0){
      this.setState({
        doRender: false
      })
      // this.props.removeAppointment()
      console.log("Self destruct")
      this.props.handleDeletingAvailability(this.props.id)
      return 
    }
    const numMinutesTot = Math.floor(currentY * 24 * 4  /this.props.heightSchedule) * 15
    const currentDay = Math.floor((currentX / this.props.widthSchedule) * this.props.numDays)
    
    let newStartTime = this.state.startTime.startOf('week').plus({days: currentDay})
    newStartTime = newStartTime.startOf('day').plus({minutes: numMinutesTot})
    
    this.setState({
      startTime: newStartTime
    })
    this.props.handleSingleAvailabilityChange(this.props.id, {
      color: this.state.color,
      lengthMinutes: this.state.lengthMinutes,
      startTime: this.state.startTime
    })
  };

  newStyle(){
    const {
      numDays, 
      widthSchedule, 
      heightSchedule} = this.props
    
    const { color,
      lengthMinutes, 
      startTime} = this.state

    const fullWidth = widthSchedule/numDays
    const width = fullWidth * 0.85
    const height = parseInt((lengthMinutes) / (24 * 60) * heightSchedule)

    let x, y
    if(this.state.isDragging){
      x = this.state.currentX - width/2
      y = this.state.currentY
    } else {
      x = this.posX(startTime, numDays, widthSchedule) + fullWidth * 0.15
      y = this.posY(startTime, heightSchedule)
    }
    
    let border = 0
    if(this.state.currentX < 0 || this.state.currentY < 0){
      border = 5
    }
    return {
      background: color,
      borderWidth: border,
      width: width + 'px',
      height: height + "px",
      transform: `translate(${x}px, ${y}px)`}
  }

  handleStyle(){
    const {
      numDays, 
      widthSchedule, 
      heightSchedule} = this.props
    
    const { color,
      lengthMinutes, 
      startTime} = this.state

    const fullWidth = widthSchedule/numDays
    const width = fullWidth * 0.85

    const height = parseInt((10) / (24 * 60) * heightSchedule)
    
    let x, y
    if(this.state.isDragging){
      x = this.state.currentX - width/2
      y = this.state.currentY + parseInt((lengthMinutes - 10) / (24 * 60) * heightSchedule)
    } else {
      x = this.posX(startTime, numDays, widthSchedule) + fullWidth * 0.15
      y = this.posY(startTime, heightSchedule) + parseInt((lengthMinutes - 10) / (24 * 60) * heightSchedule)
    }

    return {
      height: height + "px",
      width: width + 'px',
      transform: `translate(${x}px, ${y}px)`}
  }
    
  render(){
    const { id} = this.props
    
    const style = this.newStyle()
    const handleStyle = this.handleStyle()
    return (
      <div key={"master_availability_" + id} >
        {this.state.doRender ? <div><div 
          onMouseDown={this.handleMouseDown}
          key={id} 
          id={"availability_"+id} 
          className="available" 
          style={style}
          >
        </div>
        <div 
        className="extentHandle"
        style={handleStyle}
        onMouseDown={this.handleExtentMouseDown}/>
        </div>
        : null }
    </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    numDays: state.schedule.numDays,
    widthSchedule: state.schedule.widthSchedule,
    heightSchedule: state.schedule.heightSchedule,
  }
}

export default connect(mapStateToProps)(Availability)


