import React, { Component } from 'react'
import { connect } from 'react-redux'
import '../../sidebar/sidebar.css';
import {scheduleWidthUpdate, scheduleHeightUpdate} from '../../../actions/scheduleActions'
import ColorSelector from './selectColor'

class SideBar extends Component {
  constructor(props) {
    super(props);
}

handleNameChange = () => {
    this.props.handleNameChange(document.getElementById('nameClient').value)
}
  render(){
    const { clients, clientID } = this.props
    const client = clients[clientID]

    return (
    <div className="sidebar-background" id="sidebar-background" key="sidebar-background">
    <label>
        Name:
        <input 
        type="text" 
        name="nameClient" 
        id="nameClient"
        defaultValue={client.name} 
        onChange={this.handleNameChange}/>
    </label>
    <ColorSelector onSelectColor={this.props.handleColorChange} defaultColor={client.color}/>
    
    </div>
    )
  }
}


const mapStateToProps = (state) => {
  return {
    clients: state.clients,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    updateWidth: (newWidth) => dispatch(scheduleWidthUpdate(newWidth)),
    updateHeight: (newHeight) => dispatch(scheduleHeightUpdate(newHeight)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(SideBar)


