import React, { Component } from 'react'
import { connect } from 'react-redux'
import Availability from './available/index'
import {position2time} from '../converters/position2time'
import {scheduleWidthUpdate, scheduleHeightUpdate} from '../../actions/scheduleActions'
import ScheduleLayout from '../schedule/layout'
import '../schedule/schedule.css';

class Schedule extends Component {
  constructor(props) {
    super(props);
    this.onResize = this.onResize.bind(this);
    this.state = {
      activeColor : null,
      colorOptions: 
          {red: '#b00',
          orange: '#d80',
          green: '#090',
          blue: '#00b',
          },
      availability: []    
    }
}
  componentDidMount() {
    const {clients, clientID} = this.props
    const client = clients[clientID]

    this.setState({
      availability: client.available ? client.available: []
    })

    window.addEventListener("resize", this.onResize);
    this.onResize();
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.onResize);
  }
  onResize(){
    let totalWidthVar = document.getElementById('schedule-background').offsetWidth
    let totalHeightVar = document.getElementById('schedule-background').offsetHeight

    this.props.updateWidth(totalWidthVar)
    this.props.updateHeight(totalHeightVar)
  }

  handleClick = (colorName) => {
    colorName = colorName === this.state.activeColor ? null: colorName
    this.setState({
      activeColor: colorName
    })
  }

  onClick = ({ clientX, clientY}) => {
    if(this.state.activeColor){
      const rect = document.getElementById("schedule-background").getBoundingClientRect();
      let x = clientX - rect.left
      let y = clientY - rect.top;
      const {startTimeWeek, numDays, totalWidth, totalHeight} = this.props
      const time = position2time(x, y, startTimeWeek, numDays, totalWidth, totalHeight)
      this.state.activeColor
      let avals = this.state.availability
      const newAval = {
        color: this.state.activeColor,
        lengthMinutes: 30,
        startTime: time
      }
      avals.push(newAval)
      this.setState({
        availability: avals
      })
      this.props.handleAvailabilityChange(avals)
    }
  }

  handleSingleAvailabilityChange = (id, newAval) => {
    let avals = this.state.availability
    if(id === undefined){
      return
    }
    avals[id] = newAval
    this.setState({
      availability: avals
    })
    this.props.handleAvailabilityChange(avals)
  }

  handleDeletingAvailability = (id) => {
    let avals = this.state.availability
    if(id === undefined){
      return
    }
    delete avals[id]
    this.setState({
      availability: avals
    })
    this.props.handleAvailabilityChange(avals)
  }

  render(){
    const colorObjects = Object.keys(this.state.colorOptions).map(colorName => {
      const border = this.state.activeColor === colorName ? "#f0f": "transparent"
      return (
        <div key={colorName} className="col s2">
        <div 
          className="button"
          style = {{
            background: this.state.colorOptions[colorName],
            borderColor: border}}
          onClick={() => this.handleClick(colorName)}>{colorName}</div>
        </div>
      )
    })
    const {availability} = this.state
    const availableObjects = Object.keys(availability).length ? (
      Object.keys(availability).map(id => {
        const aval = availability[id]
        return (
          <div key={"available_" + id}>
           <Availability 
              id = {id}
              startTime = {aval.startTime}
              color = {aval.color}
              lengthMinutes = {aval.lengthMinutes} 
              handleSingleAvailabilityChange = {this.handleSingleAvailabilityChange}
              handleDeletingAvailability = {this.handleDeletingAvailability}/>
          </div>
        )
      })
    ) : (
      <div className="center"></div>
    );  

    return (
      <div>
        <div className="row" style={{height: "5vh"}}>
          {colorObjects}
        </div>  
        <div className="row scheduleContainer">
      <div 
      className="schedule-background" 
      id="schedule-background" 
      key="schedule-background"
      onClick={this.onClick}>
        
        
        <ScheduleLayout startTime={0} stopTime={24}/>
        <div>{availableObjects}</div>
      </div>
    </div>
    </div>
    )
  }
}


const mapStateToProps = (state) => {
  return {
    clients: state.clients,
    startTimeWeek: state.schedule.startTimeWeek,
    numDays: state.schedule.numDays, 
    totalWidth: state.schedule.widthSchedule, 
    totalHeight: state.schedule.heightSchedule,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    updateWidth: (newWidth) => dispatch(scheduleWidthUpdate(newWidth)),
    updateHeight: (newHeight) => dispatch(scheduleHeightUpdate(newHeight)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Schedule)


