import React, { Component } from 'react'
import Schedule from './schedule/Schedule'
import SideBar from './sidebar/index'
import './home.css';

class Home extends Component {
  render(){
    return (
      <div className="row">
        <div className="col s2">
          <SideBar />
        </div>
        <div className="col s10 scheduleContainer">
            <Schedule />
      </div>
    </div>
    )
  }
}

export default Home


