import { DateTime } from 'luxon'

const initState = {
  numDays: 5,
  widthSchedule: 0,
  heightSchedule: 0,
  timeZone: 'Europe/Amsterdam',
  startTimeWeek: DateTime.fromMillis(-3*24 * 3600000).setZone(this.timeZone),
  showAvailability: null,
  activeClient: null,
}


const scheduleReducer = (state = initState, action) => {

  if(action.type === 'UPDATE_SCHEDULE_WIDTH'){
   return {
     ...state,
     widthSchedule: action.newWidth
   }
  }
  if(action.type === 'UPDATE_SCHEDULE_HEIGHT'){
    return {
      ...state,
      heightSchedule: action.newHeight
    }
   }
   if(action.type === 'UPDATE_SHOW_AVAILABILITY'){
    return {
      ...state,
      showAvailability: action.id
    }
   }
   if(action.type === 'UPDATE_ACTIVE_CLIENT'){
    return {
      ...state,
      activeClient: action.id,
      showAvailability: action.id
    }
   }
  return state;
}

export default scheduleReducer


