export const scheduleWidthUpdate = (newWidth) => {
  return {
    type: 'UPDATE_SCHEDULE_WIDTH',
    newWidth
  }
}

export const scheduleHeightUpdate = (newHeight) => {
  return {
    type: 'UPDATE_SCHEDULE_HEIGHT',
    newHeight
  }
}

export const scheduleShowAvailabilityUpdate = (id) => {
  return {
    type: 'UPDATE_SHOW_AVAILABILITY',
    id
  }
}

export const scheduleActiveClientUpdate = (id) => {
  return {
    type: 'UPDATE_ACTIVE_CLIENT',
    id
  }
}


