export const updateClient = (id, newClient) => {
  return {
    type: 'UPDATE_CLIENT',
    id, 
    newClient
  }
}

export const updateClients = (clients) => {
  return {
    type: 'UPDATE_CLIENTS',
    clients
  }
}

export const deleteClient = (id) => {
    return {
      type: 'DELETE_CLIENT',
      id, 
    }
  
}

export const createClient = () => {
  const newClient = {
    name: "",
    color: "#666",
    available: []
  }
  return {
    type: 'ADD_CLIENT',
    newClient
  }
}


