import React, { Component } from 'react'
import { connect } from 'react-redux'
import {scheduleActiveClientUpdate} from '../../actions/scheduleActions'

class Client extends Component {
  constructor(props) {
    super(props);
    this.props = props
    this.state = {
        client: this.props.clients[this.props.id]
    }
  }

  newStyle(client){
      const border = this.props.activeClient === this.props.id ? 3 : 0
    return {
      background: client.color,
      borderStyle: "solid",
      borderWidth: border}
  }
  
  go2clientpage = () => {
    console.log("go to " + this.props.id)
    window.location.hash = "/client/"+ this.props.id;
    // this.props.history.push('client/' + this.props.id);
    
  }

  onClick = () => {
    if(this.props.activeClient === this.props.id){
    this.props.turnOffActiveClient()
    }else {
    this.props.turnOnActiveClient(this.props.id)
    }
  }

  render(){
    const {id} = this.props
    
    let style = this.newStyle(this.state.client)
    return (
    <div 
      onDoubleClick={this.go2clientpage}
      onClick={this.onClick}
      key={"client_" + id} 
      id={"client_" + id} 
      className="client" 
      style={style}
    >
        <p>{this.state.client.name}</p>
    </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    clients: state.clients,
    activeClient: state.schedule.activeClient
  }
}

const mapDispatchToProps = (dispatch, props) => {
    return {
      turnOnActiveClient: () => dispatch(scheduleActiveClientUpdate(props.id)),
      turnOffActiveClient: () => dispatch(scheduleActiveClientUpdate(null)),
    }
  }

export default connect(mapStateToProps, mapDispatchToProps)(Client)


