import React, { Component } from 'react';
import './sidebar.css'
class ColorSelector extends Component {
  constructor() {
    super();
    
    this.state = {
      showMenu: false,
      currentColor: '#C00',
      colorOptions: 
          {grey: '#666',
          red: '#C00',
          green: '#0C0',
          blue: '#00C',
          purple: '#C0C',
          }
    }
    
    this.showMenu = this.showMenu.bind(this);
  }
  
  showMenu(event) {
    event.preventDefault();
    
    this.setState({
      showMenu: !this.state.showMenu,
    });
  }
  
  handleClick = (colorName) => {
    this.setState({
      currentColor: this.state.colorOptions[colorName],
      showMenu: false
    })
    this.props.onSelectColor(this.state.colorOptions[colorName])
  }
  
  componentDidMount(){
    const {defaultColor} = this.props
    if(defaultColor){
      this.setState({
        currentColor: defaultColor
      })
    }
  }

  render() {

      const menuOption = Object.keys(this.state.colorOptions).map(colorName => {
        return (
          <div 
            key={colorName}
            className="menuItem"
            style = {{background: this.state.colorOptions[colorName]}}
            onClick={() => this.handleClick(colorName)}>
           {colorName}
          </div>
        )
      })
    return (
      <div>
        <div 
            className="menuButton" 
            style = {{background: this.state.currentColor}}
            onClick={this.showMenu}>
          Color
        </div>
        
        {
          this.state.showMenu
            ? (
              <div className="menu">
                {menuOption}
              </div>
            )
            : (
              null
            )
        }
      </div>
    );
  }
}

export default ColorSelector


