

export const position2time = (x, y, startTimeLuxon, numDays, totalWidth, totalHeight) => {
    const daysFromStart = Math.floor(x / totalWidth * numDays)
    const minutesFromStartDay = Math.round(y / totalHeight * 24 * 4) * 15
    return startTimeLuxon.startOf('week').plus({days: daysFromStart, minutes: minutesFromStartDay})
}


