export const appointmentUpdate = (id, newAppointment) => {
  return {
    type: 'UPDATE_APPOINTMENT',
    id, 
    newAppointment
  }
}
export const updateAppointments = (appointments) => {
  return {
    type: 'UPDATE_APPOINTMENTS',
    appointments
  }
}

export const deleteAppointment = (id) => {
  return {
    type: 'DELETE_APPOINTMENT',
    id, 
  }
}

export const createAppointment = (startTime, clientID) => {
  const newAppointment = {
    startTime,
    lengthMinutes: 30,
    client: clientID
  }
  return {
    type: 'ADD_APPOINTMENT',
    newAppointment
  }
}


