import React, { Component } from 'react'
import { connect } from 'react-redux'
import Schedule from './schedule'
import SideBar from './sidebar/sidebar'
import '../home.css';
import {updateClient, deleteClient} from '../../actions/appointmentClients'

class clientPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      client: null
    }
  }
  componentDidMount(){
    this.setState({
      client: this.props.client
    })
  }

  handleColorChange = (newColor) => {
    let client = this.state.client
    client.color = newColor
    this.setState({
      client: client
    })
  }

  handleNameChange = (newName) => {
    let client = this.state.client
    client.name = newName
    this.setState({
      client: client
    })
  }

  handleAvailabilityChange = (newAvailability) => {
    let client = this.state.client
    client.availability = newAvailability
    this.setState({
      client: client
    })
  }

  handleSave = () => {
    
    if(this.props.id){
      this.props.updateClient(this.state.client)
    }
    this.props.history.push('/');
  }

  handleDelete = () => {
    if(this.props.id){
      console.log("Delete", this.props.id)
      this.props.deleteClient(this.props.id)
    }
    this.props.history.push('/');
  }

  render(){
    const {id} = this.props
    return (
      <div className="row">
      <div className="fullSideBar col s2">
      <button onClick={this.handleSave}>Save</button>
      {/* <button onClick={this.handleDelete}>Delete</button> */}
        <SideBar 
          clientID = {id} 
          handleColorChange = {this.handleColorChange}
          handleNameChange = {this.handleNameChange}/>
      </div>
      <div className="col s10">
          <Schedule 
          clientID = {id} 
          handleAvailabilityChange = {this.handleAvailabilityChange}/>
    </div>
  </div>
    )
  }
}


const mapStateToProps = (state, ownProps) => {
  let id = ownProps.match.params.client_id;
  return {
    client: state.clients[id],
    id: id
  }
}

const mapDispatchToProps = (dispatch, props) => {
  return {
    updateClient: (newClient) => dispatch(updateClient(props.id, newClient)),
    deleteClient: (id) => dispatch(deleteClient(id))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(clientPage)


