import React, { Component } from 'react'
import { connect } from 'react-redux'
import { posY } from '../../converters/calculatePosY'
import { posX } from '../../converters/calculatePosX'

class Available extends Component {
  constructor(props) {
    super(props);
    this.props = props
    this.posX = posX
    this.posY = posY
  }

  newStyle(appointment, numDays, widthSchedule, heightSchedule){
    let width = widthSchedule/numDays
    const height = parseInt((appointment.lengthMinutes) / (24 * 60) * heightSchedule)
    let x, y
    x = this.posX(appointment.startTime, numDays, widthSchedule)
    y = this.posY(appointment.startTime, heightSchedule)
    return {
      background: appointment.color,
      width: width + 'px',
      height: height + "px",
      transform: `translate(${x}px, ${y}px)`}
  }
    
  render(){
    const { clients, appointmentID, numDays, widthSchedule, heightSchedule, id} = this.props
    const available = clients[appointmentID].available[id]
    
    let style = this.newStyle(
        available, 
        numDays, 
        widthSchedule, 
        heightSchedule)
    
    return (
    <div 
      key={"available_" + appointmentID + "_" + id} 
      id={"available_" + appointmentID + "_" + id} 
      className="available" 
      style={style}
    />
    )
  }
}

const mapStateToProps = (state) => {
  return {
    clients: state.clients,
    numDays: state.schedule.numDays,
    widthSchedule: state.schedule.widthSchedule,
    heightSchedule: state.schedule.heightSchedule,
  }
}

export default connect(mapStateToProps)(Available)


