import { DateTime } from 'luxon'

const initState = {

  }


const clientListReducer = (state = initState, action) => {
   if(action.type === 'UPDATE_CLIENT'){
     const newClient = {
       ...state,
     }
     if(action.id){
      newClient[action.id] = action.newClient
     }
    return newClient
   }
   if(action.type === 'DELETE_CLIENT'){
    let newClient = state
    delete newClient[action.id]
    return newClient
  }

   if(action.type === 'ADD_CLIENT'){
    let max = 0
    for (const property in Object.keys(state)) {
     const num = parseInt(Object.keys(state)[property])
     max = (max < num) ? num : max;
   }
   const newID = max + 1

    const newClient = {
      ...state,
    }
    newClient[newID] = action.newClient
   return newClient
  }
  if(action.type === 'UPDATE_CLIENTS'){
   return action.clients
  }
  return state;
}

export default clientListReducer


