import React, { Component } from 'react'
import { connect } from 'react-redux'
import Client from './client'
import './sidebar.css';
import { scheduleWidthUpdate, scheduleHeightUpdate } from '../../actions/scheduleActions'
import { createClient, updateClients } from '../../actions/appointmentClients'
import { updateAppointments } from '../../actions/appointmentActions'
import { DateTime } from 'luxon'

class SideBar extends Component {
  constructor(props) {
    super(props);
  }

  addClient = () => {
    this.props.createClient()
    let max = 0
    for (const property in Object.keys(this.props.clients)) {
      const num = parseInt(Object.keys(this.props.clients)[property])
      max = (max < num) ? num : max;
    }
    const newID = max + 1

    window.location.hash = "/client/" + newID;
  }

  saveState = () => {
    const data = JSON.stringify({
      clients: this.props.clients,
      appointments: this.props.appointments
    })
    const file = new Blob([data], { type: type });
    const filename = 'client_and_appointments.txt'
    const type = 'text/plain'
    if (window.navigator.msSaveOrOpenBlob) // IE10+
      window.navigator.msSaveOrOpenBlob(file, filename);
    else { // Others
      var a = document.createElement("a"),
        url = URL.createObjectURL(file);
      a.href = url;
      a.download = filename;
      document.body.appendChild(a);
      a.click();
      setTimeout(function () {
        document.body.removeChild(a);
        window.URL.revokeObjectURL(url);
      }, 0);
    }
  }

  loadState = () => {

    const fileSelected = document.getElementById('txtfiletoread');
    //Set the extension for the file 
    const fileExtension = /text.*/;
    //Get the file object 
    const fileTobeRead = fileSelected.files[0];
    //Check of the extension match 
    if (fileTobeRead.type.match(fileExtension)) {
      //Initialize the FileReader object to read the 2file 
      let fileReader = new FileReader();
      fileReader.onload = () => {
        const patient_data = JSON.parse(fileReader.result);

        //Clients
        let clients = patient_data.clients

        Object.keys(clients).forEach((id) => {
          let avals = []
          clients[id].available.forEach(aval => {
            if (aval) {
              aval.startTime = DateTime.fromISO(aval.startTime)
              avals.push(aval)
            }
          })
          clients[id].available = avals
        })
        this.props.updateClients(clients)

        //Appointments
        let appointment = patient_data.appointments
        Object.keys(appointment).forEach((id) => {
          appointment[id].startTime = DateTime.fromISO(appointment[id].startTime)
        })
        this.props.updateAppointments(patient_data.appointments)
      }
      fileReader.readAsText(fileTobeRead);
    }
    else {
      alert("Please select text file");
    }
  }

  render() {
    let { clients } = this.props
    
    const clientsArray = Object.keys(clients).length ? (
      Object.keys(clients).map(client => {
        return (
          {...clients[client], id: client}
        )
      })
    ) : (
      []
    )
    
    if (clientsArray.length) {
      clientsArray.sort(function (a, b) {
        if (a.name.toLowerCase() < b.name.toLowerCase()) { return -1; }
        if (a.name.toLowerCase() > b.name.toLowerCase()) { return 1; }
        return 0;
      })
    }

    const clientsObjects = Object.keys(clientsArray).length ? (
      clientsArray.map(client => {
        return (
          <Client key={"clientObject_" + client.id} id={client.id} />
        )
      })
    ) : (
        <div className="center">
          Nothing in here
      </div>
      );

    return (
      <div>
        <div className="row">
          <div className="col s6 button orange"
            onClick={this.saveState}>Save</div>
          <input className="col s6 button orange" type="file" id="txtfiletoread" onChange={this.loadState} />
        </div>
        <div className="button green"
          onClick={this.addClient}>Add Client</div>
        <div className="fullSideBar" id="sidebar-background" key="sidebar-background">
          <div className="sidebar-background">
            {clientsObjects}
          </div>
        </div>
      </div>
    )
  }
}


const mapStateToProps = (state) => {
  return {
    clients: state.clients,
    appointments: state.appointments
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    updateWidth: (newWidth) => dispatch(scheduleWidthUpdate(newWidth)),
    updateHeight: (newHeight) => dispatch(scheduleHeightUpdate(newHeight)),
    createClient: () => dispatch(createClient()),
    updateClients: (clients) => dispatch(updateClients(clients)),
    updateAppointments: (appointments) => dispatch(updateAppointments(appointments)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(SideBar)


