

export const posX = (startTimeLuxon, numDays, totalWidth) => {
    const startDay = startTimeLuxon.startOf('day').toMillis() - startTimeLuxon.startOf('week').toMillis()
    return Math.round((startDay)/(24*60*60*1000)) / numDays * totalWidth
}


