

export const posY = (startTimeLuxon, totalHeight) => {
    const hoursFromStartDay = startTimeLuxon.toFormat("HH")
    const minutesFromStartHour = startTimeLuxon.toFormat("mm")
    const minutesInThisDay= parseInt(hoursFromStartDay) * 60 + parseInt(minutesFromStartHour)
    return parseInt(minutesInThisDay / (24 * 60) * totalHeight)
}


