import React, { Component } from 'react'
import { connect } from 'react-redux'

class ScheduleLayout extends Component {
    constructor(props) {
        super(props);
        this.props = props
    }

    styleHour(hour, totalHours, widthSchedule, heightSchedule){
        const height = parseInt((heightSchedule) / totalHours)
        let x, y
        x = 0
        y = hour * height
        return {
          width: widthSchedule + 'px',
          height: height + "px",
          transform: `translate(${x}px, ${y}px)`}
      }

      styleDay(day, numDays, widthSchedule, heightSchedule){
        const x = parseInt((day) / numDays * widthSchedule)
        const y = 0
        const width = parseInt((widthSchedule) / numDays)
        return {
          width: width + 'px',
          height: heightSchedule + "px",
          transform: `translate(${x}px, ${y}px)`}
      }

    render(){
        // startTime, stopTime,
        const {startTime, stopTime, numDays, widthSchedule, heightSchedule} = this.props
        
        // Hours
        const hourBars = []
        for (let hour = startTime; hour < stopTime; hour++) { 
            let style = this.styleHour(hour, stopTime - startTime, widthSchedule, heightSchedule)
            hourBars.push(
            <div 
                key={"hourBar" + hour} 
                id={"hourBar" + hour} 
                className="hourBar" 
                style={style}
                >
            {hour}h
            </div>
            )
        }

        const dayLines = []
        for (let day = 0; day < numDays; day++) { 
            let style = this.styleDay(day, numDays, widthSchedule, heightSchedule)
            dayLines.push(
            <div 
                key={"dayLine" + day} 
                id={"dayLine" + day} 
                className="dayLine" 
                style={style}
                >
            </div>
            )
        }
        
        return (
            <div key="layoutSchedule">
            {hourBars}
            {dayLines}
            </div>
        )
      }
}

const mapStateToProps = (state) => {
    return {
        numDays: state.schedule.numDays,
        widthSchedule: state.schedule.widthSchedule,
        heightSchedule: state.schedule.heightSchedule,
    }
}
  
  
export default connect(mapStateToProps)(ScheduleLayout)


